import React from "react";
import { PageHeader, Spin, Table, Tooltip } from "antd";
import { useQuery } from "react-query3";
import DeleteAllNotification from "components/ui/DeleteAllNotification";
import DeleteNotificationButton from "components/ui/DeleteNotificationButton";
import dayjs from "dayjs";
import { DeleteFilled } from "@ant-design/icons";

const columns = [
    {
        title: 'Title',
        dataIndex: 'title',
        key: 'title',
        onFilter: (value, record) => record.title.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.title.localeCompare(b.title),
    },
    {
        title: 'Created Time',
        dataIndex: 'createdTime',
        key: 'createdTime',
        onFilter: (value, record) => record.createdTime.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.createdTime.localeCompare(b.createdTime),
        render: (text, record) => {
            const start = dayjs(record?.createdTime);
            return (
                <Tooltip title={dayjs(record?.createdTime).toString()}>
                    {dayjs().to(start)}
                </Tooltip>
            )
        }
    },
    {
        title: 'Description',
        dataIndex: 'description',
        key: 'description',
        onFilter: (value, record) => record.description.startsWith(value),
        filterSearch: true,
        sorter: (a, b) => a.description.localeCompare(b.description),
    },
    {
        title: 'Action',
        dataIndex: 'action',
        key: 'action',
        render: (text, record) => {
            return (
                <DeleteNotificationButton
                    resource={record}
                    size="small"
                    icon={<DeleteFilled />}
                />
            )
        }
    },
];

export default function Notifications() {
    const { data: notifications, isLoading } = useQuery<Notification[]>("/notification", {
        refetchOnWindowFocus: false,
        keepPreviousData: true,
    });

    return (
        <PageHeader title="Notifications" subTitle="Notifications generated by the system."
            extra={
                [
                    <DeleteAllNotification danger />
                ]
            }>

            {isLoading && <Spin />}
            {!isLoading && <Table dataSource={notifications} columns={columns} tableLayout="fixed" />}
        </PageHeader>
    )


}
